import React, { Component } from 'react';
import styled from 'styled-components';
import Gx from '@tgrx/gx';
import { H4, H2 } from 'components/Typography';
import Wrapper from 'components/Wrapper';
import Card from 'components/Card';
import Input from 'components/Input';
import Metascraper from 'metascraper'

const Container = styled(Wrapper)`
`;

const Content = styled(Wrapper)`
margin-top: 100px;
`;

class News extends Component {
  constructor(props) {
    super(props);
    // Don't do this!
    this.state = { 
      color: props.color,
      data: {},
    };
   }
   componentDidMount(){
    const unfurl = require('simple-unfurl');

    (async () => {
      // const urls = ['http://www.tothenew.com/blog/javascript-slice-vs-substring-vs-substr/']
      const url = 'https://cors.io/?https://www.valio.fi/paasiainen/pasha/'
      const mapping = { title: 'title', description: 'description', imageUrl: 'twitter:image:src' }
      try {
        const pageData = await unfurl(url, mapping)
        this.setState({data: pageData})
        console.log('pageData', pageData);
      } catch(err) {
        console.log('Erorr Occurred --\n', err)
      }
    })()
   }

  render() {
    
   
    return (
      <Container>
        <Content>
        <Gx col={6}>
         <H4>Search</H4>
        </Gx>
       
        <Gx col={6}>
          <H4>
          <Input />
            </H4>
        </Gx>  
        <Card data={this.state.data} />

        </Content>
      
      </Container>
  
    )
    
 
  }
}





export default News;
